<template>
  <el-card>
    <template #header>
      <slot></slot>
      <div style="float: right">
        <span v-if="lang == 'javascript' && showStrictBtn && $showStrictBtn"
          >严格模式</span
        >
        <el-switch
          v-if="lang == 'javascript' && showStrictBtn && $showStrictBtn"
          v-model="useStrict"
          active-text="strict"
          inactive-text="sloppy"
          inline-prompt
          style="margin-left: 10px; margin-right: 20px"
        />
        <span v-if="lang == 'html'">使用新开窗口</span>
        <el-switch
          v-if="lang == 'html'"
          v-model="useNewWindow"
          active-text="new"
          inactive-text="current"
          inline-prompt
          style="margin-left: 10px; margin-right: 20px"
        />
        <el-button
          :data-help="`${
            lang == 'html'
              ? '新开窗口预览|在新窗口中运行这个html查看效果'
              : '运行代码|运行这段JavaScript代码'
          }`"
          @click="run()"
          circle
          :icon="VideoPlay"
        ></el-button>
      </div>
    </template>
    <div :style="`height:${height}px;`" ref="editorElement"></div>
    <el-card
      v-show="showResult"
      :body-style="
        lang == 'javascript'
          ? { padding: '0px', paddingLeft: '35px' }
          : { padding: '0px' }
      "
      ><ul v-if="lang == 'javascript'" ref="result" />
      <iframe v-else height="500" :src="htmlUrl" style="width:100%;border:none"/>
    </el-card>
  </el-card>
</template>

<script setup>
import { VideoPlay } from "@element-plus/icons-vue";
//eslint-disable-next-line
import { onMounted, ref } from "vue";
import * as console_html from "console-log-html";
import { useObjectUrl, useStorage } from "@vueuse/core";
import { ElCard, ElButton, ElSwitch } from "element-plus";
const showStrictBtn = useStorage("showStrictBtn", false);
const $showStrictBtn = ref(true);
const useNewWindow = ref(true);
//eslint-disable-next-line
const props = defineProps(["modelValue", "lang", "innerData", "useStrict"]);
//eslint-disable-next-line
const { lang = "javascript" } = props;
//eslint-disable-next-line
const emit = defineEmits(["update:modelValue"]);
const editorElement = ref();
let editor;
const result = ref();
const showResult = ref(false);
const height = ref();
const useStrict = ref(true);
if (props.useStrict == "hide-no") {
  $showStrictBtn.value = false;
  useStrict.value = false;
} else if (props.useStrict == "hide") {
  $showStrictBtn.value = false;
} else if (props.useStrict) {
  //eslint-disable-next-line
  useStrict.value = props.useStrict;
}

let url,
  htmlUrl = ref("");
window.editor = editor;
function run() {
  if (props.lang == "html") {
    let html =
      `<meta charset="utf-8" />${
        props.modelValue.indexOf("<title>") != -1
          ? ""
          : "<title>小可爱的练习</title>"
      }` + props.modelValue;
    url = useObjectUrl(new Blob([html], { type: "text/html,charset=UTF-8" }));
    if (useNewWindow.value) {
      window.open(
        url.value,
        "小可爱的html练习",
        `width=${innerWidth / 2},height=${innerHeight}`
      );
    } else {
      showResult.value = true;
      htmlUrl = url;
    }
  } else {
    if (!showResult.value) showResult.value = true;
    console_html.connect(result.value);
    try {
      if (props.innerData) {
        (function () {
          new Function(
            ...props.innerData?.names,
            (useStrict.value ? `'use strict';` : "") + props.modelValue
          )(...props.innerData?.func);
        })();
      } else {
        (function () {
          new Function(
            (useStrict.value ? `'use strict';` : "") + props.modelValue
          )();
        })();
      }
    } catch (e) {
      console.error(e);
    }
    console_html.disconnect();
  }
}

onMounted(() => {
  window.setEditorCallback.push(() => {
    editor = window.monaco.editor.create(editorElement.value, {
      value: props.modelValue,
      language: lang,
      theme: "vs-dark",
      scrollbar: false,
      fontSize: 16,
      scrollBeyondLastColumn: false,
      scrollBeyondLastLine: false,
      cursorSmoothCaretAnimation: true,
      editorOptions: {
        automaticLayout: true, // 自动布局,
        autoIndent: true, //自动缩进
      },
    });
    height.value =
      editor.getModel().getLineCount() *
      editor.getOption(window.monaco.editor.EditorOption.lineHeight);
    var callback = () => {
      emit("update:modelValue", editor.getValue());
      height.value =
        (editor.getModel().getLineCount() + 1) *
        editor.getOption(window.monaco.editor.EditorOption.lineHeight);
      editor.layout();
    };
    editor.onDidChangeModelContent(() => {
      callback();
    });
    setTimeout(() => {
      editor.layout();
    }, 500);
  });
});
</script>

<style>
.text-danger {
  color: var(--el-color-danger);
}
.text-warning {
  color: var(--el-color-warning);
}
</style>
